import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { LocalNotifications } from '@ionic-native/local-notifications/ngx';
import { NavController, AlertController, Platform } from '@ionic/angular';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  user = {};
  uid = {};
  items = [];
  
  constructor(private afAuth: AngularFireAuth, private db: AngularFirestore, public navCtrl: NavController, 
    private plt: Platform, private localNotifications: LocalNotifications, alertCtrl: AlertController) {
    this.afAuth.authState.subscribe(user => {
      if (user)
        this.user = user;
        this.uid = user.uid;
    });
    this.plt.ready().then((readySource) => {
      this.localNotifications.on('click').subscribe(async notification => {
        let json = JSON.parse(notification.data);
        let alert = alertCtrl.create({
        header: notification.title,
        message: json.mydata
      });
      (await alert).present();
      });
    });
  }

scheduleNotification() {
  this.localNotifications.schedule({
    id: 1,
    title: 'Attention',
    text: 'jjjbjubjb Notification',
    data: { mydata: 'My hidden message this is' },
    trigger: {at: new Date(new Date().getTime() + 5 * 1000)}
  });
}

  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (!user)
        return;
      this.db.collection(`users/${this.uid}/counters`).snapshotChanges().subscribe(colSnap => {
        this.items = [];
        colSnap.forEach(a => {
          let item = a.payload.doc.data();
          this.items.push(item);
        });
      });
    });
  }

  signOut() {
    this.afAuth.signOut().then(() => {
      location.reload();
    });
  }
}
